.height-100 {
    height: 100vh;
}

@media screen and (min-width: 768px) {
    .d-wrapper {
        margin: 0;
        padding-left: calc(var(--nav-width) + 2rem)
    }

    .header {
        height: calc(var(--header-height) + 1rem);
        padding: 0 2rem 0 calc(var(--nav-width) + 4.5rem)
    }

    .header_img {
        width: 40px;
        height: 40px
    }

    .header_img img {
        width: 45px
    }

    .l-navbar {
        left: 0;
        padding: 0;
    }

    .show {
        width: calc(var(--nav-width) + 156px)
    }

    .body-pd {
        padding-left: calc(var(--nav-width) + 188px)
    }
}


.cards-of-hodas{
    height:250px;
}



.gridded-tc{
    display: grid;
    grid-template-columns: 30% 30% 30%;
    grid-column-gap: 20px;
    grid-row-gap: 20px;

}


.a-grid{
    grid-column-gap: 50px;
}

.notif-c li{
